
import photo from '../../../static/userphoto.jpeg';
import upload from "../element/upload.vue"
import stepsbar from "../element/stepsBar.vue"
import maskbox from "../element/maskingBox.vue"
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Toast from 'vux/src/components/toast/index.vue'

import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import Datetime from 'vux/src/components/datetime/index.vue'


export default {
name: 'page',
directives: {
TransferDom
},
components: {
XHeader,
XButton,
Popup,
XTextarea,
Group,
Cell,
upload,
Toast,
stepsbar,
Datetime,
DatetimePlugin,
maskbox
},
data() {
return {
title: "项目管理",
type: '1',
uid: 0,
proid: 0,
actionshow: false,
content: "",
actionflow: false,
selectflow: "",
fid: 1,
toastText: "",
toastshow: false,
selectTime: "",
visibility: false,
schedulePosition: 0,
showimg:'',
isShowMask:false,
broadcast: [{
"id": 9,
"uid": 35,
"name": "申花壹号院5幢222室",
"z_uid": 2,
"starttime_d": 1537804800,
"image": "http://47.97.109.9/upload/images/74a597bb80881faccba156a23ea08f9f.jpg",
"project_us": "[\"3\",\"4\",\"6\"]",
"state": 0,
"type": 0,
"area": 120,
"leader_id": 14,
"staff_share": 0,
"company": "家视装饰有限公司",
"project_us_count": 4,
"leader": "销售1号",
"stage": "水电布线",
"speed": 20
}],
flow: [],
flows: [],
project: {
"id": 8,
"uid": 43,
"name": "尚品半岛6-802",
"z_uid": 15,
"image": "http://web.yuegames.com/upload/images/9d6c40f07aace6821f73b1d840e62282.jpg",
"project_us": [{
"name": "陈砚茫",
"phone": "13868849152",
"job": "设计师",
"avatar": "http://web.yuegames.com/upload/images/3d5aed77df6e7a12466463fc346ae411.png"
}],
"state": 2,
"type": 0,
"area": 180,
"style": null,
"leader_id": 16,
"cameras": null,
"headurl": "http://47.97.109.9/headurl/43.jpg"
},
uploadUrl: "https://www.homeeyes.cn/api/upload_broad_image",
imgUrl: {}

}
},
created() {
this.proid = this.$route.params.proid;
this.uid = this.util.getUid();
if(this.uid == null || this.uid == undefined) {
this.uid = 3;
}
this.initPro();
},
methods: { //初始化事件
initPro: function() {
let params = {
uid: this.uid,
pro_id: this.proid
}
this.$axios.post(this.umcons.serviceAddress.PROJECT_FLOW, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
var data = response.data.data;
this.broadcast = data.broadcast;
this.flow = data.flow;
this.flows = data.flow;
this.selectflow = data.flow[0].name;
this.project = data.project;

} else {
this.util.showAlert("加载失败");
}

}.bind(this));

},onMaskClose:function(state){
this.isShowMask=state;
},clickImg:function(src){
this.showimg=src;
this.isShowMask=true;
},
onAddBroadcast: function() {
this.actionshow = false;
let params = {
pro_id: this.proid,
f_id: this.fid,
uid: this.uid,
content: this.content,
image: this.imgUrl
}
this.$axios.post(this.umcons.serviceAddress.CREATE_BROADCAST, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.util.showAlert(response.data.mes);
} else {
this.util.showAlert("加载失败");
}

}.bind(this));
},
onAmendSchedule: function(nowtime) {
var flow = this.flow[this.schedulePosition];
if(flow.state >= 0) {
let params = {
pro_id: flow.pro_id,
f_id: flow.id,
state: flow.state + 1,
time: nowtime
}
console.log('plugin confirm', params)
this.$axios.post(this.umcons.serviceAddress.EDIT_FLOW_STATE, params).then(function(response) {
var error = response.data.error;
if(error == 0) {
this.util.showAlert(response.data.mes);
this.toastText = "修改成功";
this.toastshow = true;

setTimeout(this.initPro(), 500);
} else {
this.toastText = "修改失败";
this.toastshow = true;
}

}.bind(this));
}
},
showPlugin: function(index) {
this.schedulePosition = index;
this.visibility = true;
},
onEvent(event) {
console.log('on', event)
}, //选择时间
change(data) {
this.onAmendSchedule(data);
console.log('on', data)
},
onChange(index) {
this.selectflow = this.flow[index].name;
this.fid = this.flow[index].id;
this.actionflow = false;

},
formateState: function(state) {
if(state == 0) {
return "点击开始"
} else if(state == 1) {
return "确认完成"
} else {
return "已经完成"
}

},
imageCall: function(data) {
this.imgUrl = data;
this.actionshow = false
setTimeout(this.initPro(), 500);

},
getLenght: function(array) {
if(array.length > 0) {
return true;
} else {
return false;
}
console.log("图片：" + array)
}
}
}



